@font-face
{
  font-family: gg;
  src: url('./font.ttf');
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body
{
  margin-bottom: 3%;
}

#nav-panel
{
  margin-top: 6%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  background-color: rgba(133,200,114, 0.9);
  border: 2px solid aquamarine;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 15px;
  align-items: center;
  border-bottom: none;
}

.nav-item
{
  color: white;
  display: inline;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  padding: 10px 0px;
  border: 1px solid white;
  text-align: center;
  text-decoration: none;
  font-family: gg;
}

#log
{
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

#sign
{
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}


.active
{
  background-color: rgb(54, 240, 178);
}

.inactive
{
  background-color:rgba(133,200,114, 0.9);
}

.belowNav
{
  font-family: gg;
  display: block;
  margin-left: auto;
  align-items: center;
  margin-right: auto;
  background-color: rgba(133,200,114, 0.9);
  width: 80%;
  padding: 15px;
  border: 2.1px solid aquamarine;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  color: white;
  padding-top: 4%;
  padding-bottom: 5%;
}

input
{
  border-radius: 5px;
  border: 0.5px solid white;
  background-color: rgba(133,200,114, 0.9);
  padding: 10px;
  color: white;
  font-family: gg;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

label
{
  margin-left: auto;
  margin-right: auto;
  display: block;
  padding-left: 2%;
}

@media only screen and (min-width: 1280px)
{
  body
  {
    margin-bottom: 3%;
  }
  .belowNav
  {
    padding-top: 3%;
    padding-bottom: 3%;
  }

  #nav-panel
  {
    margin-top: 2%;
  }
}

.button
{
  font-family: gg;
  margin-right: auto;
  margin-left: auto;
  display: block;
  width: 91.5%;
  border: 2px solid white;
  border-radius: 5px;
  background-color:rgba(133,200,114, 0.9);
  color:white;
  height: 38px;
}

.button:hover
{
  background-color: rgb(54, 240, 178);
  transition: background-color 1s linear;
}